import { Component, OnInit, Input, Injectable } from '@angular/core';

@Injectable()
@Component({
  selector: 'app-datepicker',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css', './app.component.skin-metal.css']
})
export class AppComponent implements OnInit {

  currentDate: any;
  currentDay: number;

  renderedMonth: number;
  renderedYear: number;

  monthJanuary: number = 0;
  monthDecember: number = 11;
  numbOfDaysToShow: number = 42;

  numbOfDaysInCurrentMonth: number;
  numbOfDaysBeaforeTheCurrentMonth: number;
  numbOfDaysAfterTheCurrentMonth: number;

  daysInCurrentMonth: Array<number> = [];
  daysBeaforeTheCurrentMonth: Array<any> = [];
  daysAfterTheCurrentMonth: Array<any> = [];

  maxDate: any = null;
  minDate: any = null;
  selectedDays: Array<string> = [];

  monthArray: Array<string> = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
  daysArray: Array<string> = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

  constructor() { }

  ngOnInit(): void {
    this.currentDate = new Date();
    this.currentDay = this.currentDate.getDate() - 1;
    this.renderedMonth = this.currentDate.getMonth();
    this.renderedYear = this.currentDate.getFullYear();
    this.settings();
  }

  settings(): void {
    this.numbOfDaysInCurrentMonth = this.calcNumbOfDaysInMonth(this.renderedMonth, this.renderedYear);
    this.numbOfDaysBeaforeTheCurrentMonth = this.getNumbOfDaysBeaforeRenderedMonth(this.renderedMonth, this.renderedYear);
    this.numbOfDaysAfterTheCurrentMonth = (this.numbOfDaysToShow - (this.numbOfDaysInCurrentMonth + this.numbOfDaysBeaforeTheCurrentMonth));

    this.setNumbArray('current', this.daysInCurrentMonth, this.numbOfDaysInCurrentMonth);
    this.setNumbArray('before', this.daysBeaforeTheCurrentMonth, this.numbOfDaysBeaforeTheCurrentMonth);
    this.setNumbArray('after', this.daysAfterTheCurrentMonth, this.numbOfDaysAfterTheCurrentMonth);
  }

  renderCalendar(): void {
    this.clearDataArrays();
    this.settings();
  }

  setNumbArray(name: string, array: any[], numbOfDays: number) {
    if (name !== 'before') {
      for (let i = 1; i < numbOfDays + 1; i += 1) {
        array.push(i);
      }
      return;
    }
    // 'before' needs special treatment, coz we need last days
    let prevMonthTotalDays = this.calcNumbOfDaysInMonth(this.renderedMonth - 1, this.renderedYear);
    for (let i = 1; i < numbOfDays + 1; i += 1) {
      array.unshift(prevMonthTotalDays--);
    }
  }

  calcNumbOfDaysInMonth(month: number, year: number) {
    return new Date(year, month + 1, 0).getDate();
  }

  clickHandlerDay(event: any) {
    // tslint:disable-next-line:curly
    if (event.ctrlKey === true && event.shiftKey === true) return;
    const currentDay = new Date(this.renderedYear, this.renderedMonth, event.target.innerText);

    if (event.ctrlKey === true) {
      if (this.selectedDays.indexOf(currentDay.toDateString()) > -1) {
        const index = this.selectedDays.indexOf(currentDay.toDateString());
        this.selectedDays.splice(index, 1);
        return this.calculateFromAndTo();
      }
      this.selectedDays.unshift(currentDay.toDateString());
      return this.calculateFromAndTo();
    }

    if (event.shiftKey === true) {

      if (this.selectedDays.length === 0) {
         this.selectedDays.push(currentDay.toDateString());
         this.paintSpaceBetweenSelectedCells(this.selectedDays[0], currentDay.toDateString());
         this.calculateFromAndTo();
         return;
      }

      this.selectedDays = [this.selectedDays[0]];

      if (this.selectedDays[0] === currentDay.toDateString()) {
        return this.calculateFromAndTo();
      }
      this.selectedDays.push(currentDay.toDateString());
      this.paintSpaceBetweenSelectedCells(this.selectedDays[0], currentDay.toDateString());
      return this.calculateFromAndTo();
    }

    this.clearAllCheckedDays();
    this.selectedDays.push(currentDay.toDateString());
    this.calculateFromAndTo();
  }

  calculateFromAndTo(): void {
    const arr: Array<any> = [];
    for (let i = 0; i < this.selectedDays.length; i += 1) {
      arr.push(new Date(this.selectedDays[i]));
    }
    if (arr.length > 0) {
      this.maxDate = new Date(Math.max.apply(null, arr));
      this.minDate = new Date(Math.min.apply(null, arr));
    } else {
      this.clearAllCheckedDays();
    }
  }

  clearAllCheckedDays(): void {
    this.selectedDays = [];
    this.maxDate = null;
    this.minDate = null;
  }

  yearArrowClickHandle(event: any): void {
    const target = event.target.classList[1];
    switch (target) {
      case 'next-year':
        this.renderedYear += 1;
        break;
      case 'previous-year':
        this.renderedYear -= 1;
        break;
      default:
    }
    this.renderCalendar();
  }

  monthArrowClickHandle(event: any): void {
    const target = event.target.classList[1];
    switch (target) {
      case 'next-month':
        this.nextMonthClickHandle();
        break;
      case 'previous-month':
        this.previousMonthClickHandle();
        break;
      default:
    }
    this.renderCalendar();
  }

  nextMonthClickHandle(): void {
    this.renderedMonth === this.monthDecember ?
      (this.renderedYear += 1) && (this.renderedMonth = this.monthJanuary) :
      (this.renderedMonth += 1);
  }

  previousMonthClickHandle(): void {
    this.renderedMonth === this.monthJanuary ?
      (this.renderedYear -= 1) && (this.renderedMonth = this.monthDecember) :
      (this.renderedMonth -= 1);
  }

  getNumbOfDaysBeaforeRenderedMonth(month: number, year: number) {
    const date = new Date(year, month, 1);
    const firstDayOfWeekForCurrentMonth = date.toString().slice(0, 3);

    return this.daysArray.findIndex((dayOfWeek: any) => {
      return dayOfWeek === firstDayOfWeekForCurrentMonth;
    });
  }

  getCurrentDate(): void {
    this.clearDataArrays();
    this.ngOnInit();
  }

  clearDataArrays(): void {
    this.daysInCurrentMonth = [];
    this.daysBeaforeTheCurrentMonth = [];
    this.daysAfterTheCurrentMonth = [];
  }

  setMarkerForCurrentDay(day: number) {
    const currentMonth = this.currentDate.getMonth();
    const currentYear = this.currentDate.getFullYear();

    if (this.renderedYear !== currentYear) return;
    if (this.renderedMonth !== currentMonth) return;
    if (this.currentDay !== day) return;

    return true;
  }

  paintSelectedCells(index: number) {
    const currentDay = new Date(this.renderedYear, this.renderedMonth, index + 1).toDateString();

    if (this.selectedDays.indexOf(currentDay) > -1) {
      return true;
    }
  }

  paintExtremeCell(index: number) {
    const renderedDay = new Date(this.renderedYear, this.renderedMonth, index + 1).toDateString();

    if (this.minDate && this.maxDate) {
      if (this.minDate.toDateString() === renderedDay || this.maxDate.toDateString() === renderedDay) {
        return true;
      }
    }
  }

  paintSpaceBetweenSelectedCells(firstCell: string, secondCell: string): void {
    const firstDate = new Date(firstCell);
    const secondDate = new Date(secondCell);

    const startDate = +firstDate > +secondDate ? secondDate : firstDate;
    const finishDate = +firstDate > +secondDate ? firstDate : secondDate;

    const numbOfCellsBetween = this.numbOfdaysBetweenDates(finishDate, startDate);

    for (let i = 1; i < numbOfCellsBetween + 1; i += 1) {
      this.selectedDays.push(new Date(startDate.getFullYear(), startDate.getMonth(), startDate.getDate() + i).toDateString());
    }
  }

  numbOfdaysBetweenDates(date1: any, date2: any) {
    const ONE_DAY = 1000 * 60 * 60 * 24;

    if (date1.getTimezoneOffset() == date2.getTimezoneOffset()) {
      var calcDifference = Math.floor((date1 - date2) / ONE_DAY) - 1;
    } else {
      var calcDifference = Math.floor((date1 - date2) / ONE_DAY);
    }
    return calcDifference;
  }
}



